  .footer__logo-link svg {
    width: 68px;
    height: 15px;
  }
}

.footer__list {
  display: grid;
  grid-template-columns: repeat(5, max-content);
  column-gap: 40px;
  align-items: center;
}
@media (max-width: 1199px) {
  .footer__list {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    grid-template-columns: 1fr 1fr;
    margin-bottom: 19px;
    grid-auto-flow: column;
    grid-template-rows: auto auto auto;
  }
}

.footer__item {
  max-width: 70px;
  align-self: start;
}
@media (max-width: 767px) {
  .footer__item {
    padding-bottom: 15px;
  }
}

@media (max-width: 767px) {
  .footer__item:first-child {
    grid-column: 1/2;
    grid-row: 1/3;
    align-self: end;
  }
}

.footer__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
